<script>
import axios from 'axios';
import { ref, watch, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import { Pagination, Autoplay } from 'swiper/modules';

export default {
    components: {
        Swiper,
        SwiperSlide
    },
    data() {
        return {
            items: [
                { img: require('@/assets/image/thum/thum_pro1.png') },
                { img: require('@/assets/image/thum/thum_pro2.png') },
                { img: require('@/assets/image/thum/thum_pro3.png') }
            ],
            woman: [],
            man: []
        };
    },
    created() {
        // console.log('Route params:', this.$route.params);
        axios
            .get('https://kangyonggu.github.io/tint/woman.json')
            .then((response) => {
                this.woman = response.data;
            })
            .catch((error) => {
                console.error('   .', error);
            });

        axios
            .get('https://kangyonggu.github.io/tint/man.json')
            .then((response) => {
                this.man = response.data;
            })
            .catch((error) => {
                console.error('   .', error);
            });
    },
    computed: {
        selectedProduct() {
            if (!this.woman.length || !this.man.length) {
                return {};
            }

            const productId = parseInt(this.$route.params.id);
            const category = this.$route.params.category;

            // console.log('Selected product ID:', productId);
            // console.log('Selected category:', category);

            if (category === 'woman') {
                return this.woman.find((product) => product.id === productId) || {};
            } else if (category === 'man') {
                return this.man.find((product) => product.id === productId) || {};
            }
            return {};
        }
    },
    setup() {
        return {
            Pagination,
            Autoplay
        };
    }
};
</script>
