<template>
    <header class="back_header">
        <h1>{{ title }}</h1>
        <p class="back">
            <a href="#" @click.prevent="goBack"></a>
        </p>
        <p class="cart_top">
            <router-link to="/Cart"><span class="icn"></span><span class="num">3</span></router-link>
        </p>
        <section class="fav_gnb" v-if="$route.meta.InquiryWrite">
            <!-- fav_gnb start -->
            <strong>1:1 </strong>
            <router-link to="/InquiryWrite" class="write">1:1  </router-link>
        </section>
        <!-- fav_gnb end -->
    </header>
</template>

<script>
export default {
    name: 'BackHeader',
    data() {
        return {
            title: ''
        };
    },
    created() {
        this.setTitle();
    },
    watch: {
        $route() {
            this.setTitle();
        }
    },
    methods: {
        goBack() {
            this.$router.go(-1); //   
        },
        setTitle() {
            this.title = this.$route.meta.title || ' ';
        }
    }
};
</script>
